import { Entity } from './entity';

export class Lugar extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any[]): Lugar[] {
    return json.reduce((acc: Lugar[], curr: any, currIndex: number) => {
      const lugar: Lugar = new Lugar(curr);
      lugar.id = currIndex;
      return acc.concat(lugar);
    }, []);
  }

  constructor(json: any) {
    super();
    this._nombre = json.Descripcion_ZonaLugar;
  }
}
