import { Entity } from './entity';

export class Riesgo extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  get cantidad(): number {
    return this._cantidad;
  }

  set cantidad(cantidad: number) {
    this._cantidad = cantidad;
  }

  private _nombre: string;

  private _cantidad: number;

  static parse(json: any[]): Riesgo[] {
    return json.map((item: any) => new Riesgo(item));
  }

  constructor(json: any) {
    super();
    this._nombre = json.InterpretacionRiesgos;
    this._cantidad = json.totalInterpretacion;
  }
}
