import { Entity } from './entity';

export class Actividad extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any[]): Actividad[] {
    return json.reduce((acc: Actividad[], curr: any, currIndex: number) => {
      const actividad: Actividad = new Actividad(curr);
      actividad.id = currIndex;
      return acc.concat(actividad);
    }, []);
  }

  constructor(json: any) {
    super();
    this._nombre = json.descripcionProceso;
  }
}
