import { Entity } from './entity';

export class RiesgoDetalle extends Entity {
  get descripcion(): string {
    return this._descripcion;
  }

  set descripcion(descripcion: string) {
    this._descripcion = descripcion;
  }

  get controlFuente(): string {
    return this._controlFuente;
  }

  set controlFuente(controlFuente: string) {
    this._controlFuente = controlFuente;
  }

  get controlMedio(): string {
    return this._controlMedio;
  }

  set controlMedio(controlMedio: string) {
    this._controlMedio = controlMedio;
  }

  get controlIndividuo(): string {
    return this._controlIndividuo;
  }

  set controlIndividuo(controlIndividuo: string) {
    this._controlIndividuo = controlIndividuo;
  }

  private _descripcion: string;

  private _controlFuente: string;

  private _controlMedio: string;

  private _controlIndividuo: string;

  static parse(json: any[]): RiesgoDetalle[] {
    return json.map((item: any) => new RiesgoDetalle(item));
  }

  constructor(json: any) {
    super();
    this._descripcion = json.Descripcion;
    this._controlFuente = json.Crtl_Exist_Fuente;
    this._controlMedio = json.Crtl_Exist_Medio;
    this._controlIndividuo = json.Crtl_Exist_Individuo;
  }
}
