import { Entity } from './entity';

export class Peligro extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  get cantidad(): number {
    return this._cantidad;
  }

  set cantidad(cantidad: number) {
    this._cantidad = cantidad;
  }

  private _nombre: string;

  private _cantidad: number;

  static parse(json: any[]): Peligro[] {
    return json.map((item: any) => new Peligro(item));
  }

  constructor(json: any) {
    super(json.idClasificacionPeligro);
    this._nombre = json.descripcionClasificacion;
    this._cantidad = +json.cantidadDeClasifiacion;
  }
}
