import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ContextService } from './context.service';

@Injectable({
  providedIn: 'root'
})
export class ActividadService {

  constructor(
    private http: HttpClient,
    private context: ContextService
  ) { }

  getActividades(idSede: number, idMetodologia: number, idProceso: number, lugar: string): Observable<any> {
    const url = this.context.getEnvUrl().call(null, 'participacion') + '/ReporteServicio/actividades-metodologia'
      + '?id_Sede=' + idSede
      + '&idMetodologia=' + idMetodologia
      + '&id_Proceso=' + idProceso
      + '&zonaLugar=' + lugar;

    return this.http.get(url);
  }

}
