import { Entity } from './entity';

export class Metodologia extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any[]): Metodologia[] {
    return json.map((item: any) => new Metodologia(item));
  }

  constructor(json: any) {
    super(json.PK_Metodologia);
    this._nombre = json.Descripcion_Metodologia;
  }
}
