import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController, Platform } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { alertCommunicationError } from 'src/app/commons/user-feedback';
import { Actividad } from 'src/app/entities/actividad';
import { Entity } from 'src/app/entities/entity';
import { Lugar } from 'src/app/entities/lugar';
import { Metodologia } from 'src/app/entities/metodologia';
import { Peligro } from 'src/app/entities/peligro';
import { Proceso } from 'src/app/entities/proceso';
import { Riesgo } from 'src/app/entities/riesgo';
import { RiesgoDetalle } from 'src/app/entities/riesgo-detalle';
import { Sede } from 'src/app/entities/sede';
import { ActividadService } from 'src/app/services/actividad.service';
import { ContextService } from 'src/app/services/context.service';
import { EmpresaService } from 'src/app/services/empresa.service';
import { LugarService } from 'src/app/services/lugar.service';
import { MetodologiaService } from 'src/app/services/metodologia.service';
import { PeligroRiesgoService } from 'src/app/services/peligro-riesgo.service';
import { ProcesoService } from 'src/app/services/proceso.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-riesgos',
  templateUrl: './riesgos.page.html',
  styleUrls: ['./riesgos.page.scss'],
})
export class RiesgosPage implements OnInit {

  @ViewChild('content', { read: ElementRef })
  content: ElementRef;

  colors: string[] = [
    '#3366cc',
    '#dc3912',
    '#ff9900',
    '#109618',
    '#990099',
    '#0099c6',
    '#dd4477',
    '#66aa00',
    '#b82e2e',
    '#316395',
    '#3366cc',
    '#994499',
    '#22aa99',
    '#aaaa11',
    '#6633cc',
    '#e67300',
    '#8b0707',
    '#651067',
    '#329262',
    '#5574a6',
    '#3b3eac',
    '#b77322',
    '#16d620',
    '#b91383',
    '#f4359e',
    '#9c5935',
    '#a9c413',
    '#2a778d',
    '#668d1c',
    '#bea413',
    '#0c5922',
    '#743411'
  ];

  private chartSize: number;

  peligros: Peligro[];

  private riesgos: Riesgo[];

  private peligrosChart: google.visualization.PieChart;

  private riesgosChart: google.visualization.BarChart;

  private _scrollTo: string;

  private _showScrollButton = false;

  private indexPeligroSelected = -1;

  private indexRiesgoSelected = -1;

  private showPeligros = false;

  showRiesgos = false;

  showRiesgoDetalles = false;

  sedes: Sede[];

  metodologias: Metodologia[];

  procesos: Proceso[];

  lugares: Lugar[];

  actividades: Actividad[];

  riesgoDetalles: RiesgoDetalle[];

  formGroup: FormGroup;

  opcion: string;

  compareEntities = Entity.compare;

  private submitAttempt = false;

  private sedesSubscription: Subscription;

  private metodologiasSubscription: Subscription;

  private procesosSubscription: Subscription;

  private lugaresSubscription: Subscription;

  private actividadesSubscription: Subscription;

  private peligrosSubscription: Subscription;

  private riesgosSubscription: Subscription;

  private riesgoDetalleSubscription: Subscription;

  loadingPeligros: boolean;

  loadingRiesgos: boolean;

  loadingDetalleRiesgos: boolean;

  constructor(
    private formBuilder: FormBuilder,
    private context: ContextService,
    private empresaService: EmpresaService,
    private metodologiaService: MetodologiaService,
    private procesoService: ProcesoService,
    private lugarService: LugarService,
    private actividadService: ActividadService,
    private peligroRiesgoService: PeligroRiesgoService,
    private platform: Platform,
    private alertCtrl: AlertController,
    private utils: UtilsService
  ) {
    this.formGroup = formBuilder.group({
      sedeFormControl: ['', Validators.compose([Validators.required])],
      metodologiaFormControl: ['', Validators.compose([Validators.required])],
      procesoFormControl: ['', Validators.compose([])],
      lugarFormControl: ['', Validators.compose([])],
      actividadFormControl: ['', Validators.compose([])]
    });

    this.chartSize = this.getChartSize();
  }

  ngOnInit(): void {
    const getData = async (nit: string): Promise<void> => {
      await this.utils.presentLoading();
      this.sedesSubscription = this.empresaService.getSedes(nit).subscribe(
        (response: any) => {
          this.sedes = Sede.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
    };

    this.context.get().then(c => {
      const nit: string = c.empresa.id.toString();
      getData(nit);
    });
  }

  ionViewWillEnter(): void {
    this.opcion = 'risks';
  }

  ionViewDidLeave(): void {
    this.opcion = undefined;
  }

  async onSedeChanged(sede): Promise<void> {
    this.showItems(false, false, false);
    this.formGroup.controls.metodologiaFormControl.setValue('');
    this.formGroup.controls.procesoFormControl.setValue('');
    this.formGroup.controls.lugarFormControl.setValue('');
    this.formGroup.controls.actividadFormControl.setValue('');
    this.metodologias = [];
    this.procesos = [];
    this.lugares = [];
    this.actividades = [];

    await this.utils.presentLoading();
    this.metodologiasSubscription = this.metodologiaService.getMetodologiasBySede(sede.id)
      .subscribe(
        (response: any) => {
          this.metodologias = Metodologia.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
  }

  async onMetodologiaChanged(metodologia): Promise<void> {
    if (!metodologia) { return; }

    this.formGroup.controls.procesoFormControl.setValue('');
    this.formGroup.controls.lugarFormControl.setValue('');
    this.formGroup.controls.actividadFormControl.setValue('');
    this.procesos = [];
    this.lugares = [];
    this.actividades = [];

    this.subscribeToDrawPeligros(this.getIdSede(), metodologia.id);
    await this.utils.presentLoading();
    this.procesosSubscription = this.procesoService
      .getProcesosBySedeMetodologia(this.getIdSede(), metodologia.id).subscribe(
        (response: any) => {
          this.procesos = Proceso.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
  }

  async onProcesoChanged(proceso): Promise<void> {
    if (!proceso) { return; }

    this.formGroup.controls.lugarFormControl.setValue('');
    this.formGroup.controls.actividadFormControl.setValue('');
    this.lugares = [];
    this.actividades = [];

    this.subscribeToDrawPeligros(this.getIdSede(), this.getIdMetodologia(), proceso.id);
    await this.utils.presentLoading();
    this.lugaresSubscription = this.lugarService
      .getLugares(this.getIdSede(), this.getIdMetodologia(), proceso.id).subscribe(
        (response: any) => {
          this.lugares = Lugar.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
  }

  async onLugarChanged(lugar): Promise<void> {
    if (!lugar) { return; }

    this.formGroup.controls.actividadFormControl.setValue('');
    this.actividades = [];

    this.subscribeToDrawPeligros(this.getIdSede()
      , this.getIdMetodologia()
      , this.getIdProceso()
      , lugar.nombre);
    await this.utils.presentLoading();
    this.actividadesSubscription = this.actividadService
      .getActividades(this.getIdSede()
        , this.getIdMetodologia()
        , this.getIdProceso()
        , lugar.nombre)
      .subscribe(
        (response: any) => {
          this.actividades = Actividad.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
  }

  onActividadChanged(actividad): void {
    if (!actividad) { return; }

    this.subscribeToDrawPeligros(this.getIdSede()
      , this.getIdMetodologia()
      , this.getIdProceso()
      , this.getLugar()
      , actividad.nombre);
  }

  subscribeToDrawPeligros(idSede: number, idMetodologia: number, idProceso?: number, lugar?: string, actividad?: string): void {
    this.showItems(false, false, false);
    this.loadingPeligros = true;
    this.peligrosSubscription = this.peligroRiesgoService
      .getPeligrosIdentificados(idSede, idMetodologia, idProceso, lugar, actividad)
      .subscribe(
        (response: any) => {
          this.peligros = Peligro.parse(response);
          const totalPeligros: number = this.peligros.reduce(
            (acc, peligro) => acc + peligro.cantidad, 0);
          const peligrosDataTable = new google.visualization.DataTable();
          peligrosDataTable.addColumn('string', 'Peligros');
          peligrosDataTable.addColumn('number', 'Cantidad');
          peligrosDataTable.addColumn({ type: 'string', role: 'tooltip' });
          peligrosDataTable.addRows(this.peligros.map((peligro: Peligro) => {
            const porcentaje: number = peligro.cantidad / totalPeligros;
            const tooltip: string = peligro.nombre
              + '\n' + peligro.cantidad
              + ' (' + (100 * porcentaje).toFixed(1) + '%)';
            return [peligro.nombre, peligro.cantidad, tooltip];
          }));
          this.drawPeligros(peligrosDataTable);
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.loadingPeligros = false;
        }
      );
  }

  drawPeligros(dataTable: google.visualization.DataTable): void {
    this.loadingPeligros = false;
    this.showItems(true, false, false);
    this.peligrosChart = new google.visualization
      .PieChart(document.getElementById('pie-chart'));
    this.peligrosChart.draw(dataTable, this.pieOptions());
    google.visualization.events.addListener(this.peligrosChart
      , 'select'
      , this.onPeligroSelected.bind(this));
    this._showScrollButton = true;
    this._scrollTo = 'pie-chart-scroll';
  }

  onPeligroSelected(): void {
    this.loadingRiesgos = true;
    this.riesgosSubscription = this.peligroRiesgoService
      .getValoracionRiesgos(this.getIdSede()
        , this.getIdMetodologia()
        , this.getIdPeligro())
      .subscribe(
        (response: any) => {
          this.riesgos = Riesgo.parse(response);
          const totalRiesgos: number = this.riesgos.reduce((acc, riesgo) => acc + riesgo.cantidad
            , 0);
          const riesgosDataTable = new google.visualization.DataTable();
          riesgosDataTable.addColumn('string', 'Riesgos');
          riesgosDataTable.addColumn('number', 'Porcentaje');
          riesgosDataTable.addColumn({ type: 'string', role: 'tooltip' });
          riesgosDataTable.addRows(this.riesgos.map((riesgo: Riesgo) => {
            const porcentaje: number = riesgo.cantidad / totalRiesgos;
            const tooltip: string = riesgo.nombre + '\n' + Math.round(100 * porcentaje) + '%';
            return [riesgo.nombre, porcentaje, tooltip];
          }));
          this.drawRiesgos(riesgosDataTable);
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.loadingRiesgos = false;
        }
      );
  }

  onPeligroLegendSelected(index: number): void {
    this.peligrosChart.setSelection([{ column: null, row: index }]);
    this.onPeligroSelected();
  }

  drawRiesgos(riesgosDataTable: google.visualization.DataTable): void {
    this.loadingRiesgos = false;
    this.showItems(true, true, false);
    this.riesgosChart = new google.visualization
      .BarChart(document.getElementById('bar-chart'));
    this.riesgosChart.draw(riesgosDataTable, this.barOptions());
    google.visualization.events.addListener(this.riesgosChart
      , 'select'
      , this.onRiesgoSelected.bind(this));
    this._showScrollButton = true;
    this._scrollTo = 'bar-chart-scroll';
  }

  onRiesgoSelected(): void {
    this.showItems(true, true, false);
    this.loadingDetalleRiesgos = true;
    this.riesgoDetalleSubscription = this.peligroRiesgoService
      .getDetalleValoracionRiesgo(this.getIdSede()
        , this.getIdMetodologia()
        , this.getIdPeligro()
        , this.getNombreRiesgo())
      .subscribe(
        (response: any) => {
          this.riesgoDetalles = RiesgoDetalle.parse(response);
          this.loadingDetalleRiesgos = false;
          this.showItems(true, true, true);
          this._showScrollButton = true;
          this._scrollTo = 'riesgo-detalles-scroll';
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.loadingDetalleRiesgos = false;
        }
      );
  }

  private pieOptions(): google.visualization.PieChartOptions {
    return {
      colors: this.colors,
      chartArea: {
        width: '80%',
        height: '80%',
        top: 12
      },
      height: this.chartSize,
      fontName: 'Roboto',
      fontSize: 14,
      legend: {
        position: 'none'
      },
      pieHole: 0.4,
      tooltip: {
        textStyle: {
          fontSize: 13,
          fontName: 'Roboto'
        },
        trigger: 'selection'
      },
      width: this.chartSize
    };
  }

  private barOptions(): google.visualization.BarChartOptions {
    const title: string = this.peligros[this.indexPeligroSelected].nombre;

    return {
      animation: {
        startup: true,
        duration: 1000,
        easing: 'out'
      },
      chartArea: {
        // width: '65%',
        right: 40
      },
      colors: [this.colors[this.indexPeligroSelected]],
      fontName: 'Roboto',
      fontSize: 14,
      legend: {
        position: 'none'
      },
      hAxis: {
        format: '##%',
        textStyle: {
          color: '#96a0ac'
        },
        minValue: 0,
        maxValue: 1
      },
      title,
      titleTextStyle: {
        fontName: 'Roboto',
        italic: true,
        fontSize: 16
      },
      tooltip: {
        textStyle: {
          fontSize: 13,
          fontName: 'Roboto'
        },
        isHtml: false,

      },
      vAxis: {
        textStyle: {
          color: '#96a0ac'
        },
        showTextEvery: 0

      },
      width: this.chartSize
    };
  }

  getChartSize(): number {
    return Math.min(this.platform.width(), this.platform.height(), 500);
  }

  isSedeInvalid(): boolean {
    return !this.formGroup.controls.sedeFormControl.valid
      && this.submitAttempt;
  }

  isMetodologiaInvalid(): boolean {
    return !this.formGroup.controls.metodologiaFormControl.valid
      && this.submitAttempt;
  }

  isProcesoInvalid(): boolean {
    return !this.formGroup.controls.procesoFormControl.valid
      && this.submitAttempt;
  }

  isLugarInvalid(): boolean {
    return !this.formGroup.controls.lugarFormControl.valid
      && this.submitAttempt;
  }

  isActividadInvalid(): boolean {
    return !this.formGroup.controls.actividadFormControl.valid
      && this.submitAttempt;
  }

  scrollTo(): void {
    this._showScrollButton = false;
    const htmlElement: HTMLElement = this.content.nativeElement;
    const element: Element = htmlElement.querySelector('#' + this._scrollTo);
    element.scrollIntoView();
  }

  showItems(peligros: boolean, riesgos: boolean, riesgoDetalles: boolean): void {
    this.showRiesgoDetalles = riesgoDetalles;
    this.showRiesgos = riesgos;
    this.showPeligros = peligros;
  }

  hideItemPeligros(): boolean {
    return !this.showPeligros || this.showMessageNoData();
  }

  showMessageNoData(): boolean {
    return this.peligros !== undefined && this.peligros.length === 0;
  }

  showScrollButton(): boolean {
    return !this.showMessageNoData() && this._showScrollButton;
  }

  isLoadingPeligros(): boolean {
    return this.peligros === undefined;
  }

  getIdSede(): number { return this.formGroup.controls.sedeFormControl.value.id; }

  getIdMetodologia(): number { return this.formGroup.controls.metodologiaFormControl.value.id; }

  getIdProceso(): number { return this.formGroup.controls.procesoFormControl.value.id; }

  getLugar(): string { return this.formGroup.controls.lugarFormControl.value.nombre; }

  getActividad(): string { return this.formGroup.controls.actividadFormControl.value.nombre; }

  getIdPeligro(): number {
    if (this.peligrosChart.getSelection()[0]) {
      this.indexPeligroSelected = this.peligrosChart.getSelection()[0].row;
    }
    return this.peligros[this.indexPeligroSelected].id;
  }

  getNombreRiesgo(): string {
    if (this.riesgosChart.getSelection()[0]) {
      this.indexRiesgoSelected = this.riesgosChart.getSelection()[0].row;
    }
    return this.riesgos[this.indexRiesgoSelected].nombre;
  }

}
